.common-style {
    /* 共通のスタイルを定義 */

    body {
      font-family: 'Noto Sans JP', sans-serif;
      background-color: #f8f9fa;
      color: #343a40;
      margin: 0;
      padding: 0;
    }
    
    .common-style {
      max-width: 800px;
      margin: 0 auto;
      padding: 20px;
    }
    
    h1, h2, h3 {
      color: #007bff;
    }
    
    a {
      color: #007bff;
      text-decoration: none;
    }
    
    a:hover {
      text-decoration: underline;
    }
    
    p {
      margin-bottom: 15px;
    }
  }