/* Navigation.css */


.sidebar {
    position: fixed;
    top: 55px;
    left: -300px;
    width: 300px;
    height: 100%;
    background-color: rgba(255, 255, 255, 0.9);
    transition: left 0.3s ease;
    box-shadow: 2px 0 5px rgba(0, 0, 0, 0.1); /* サイドバーに影を追加 */
  }
  
  .sidebar.open {
    left: 0;
  }
  
  .sidebar-content {
    padding: 16px;
    overflow: auto; /* または overflow: scroll; */
  }
  
  button.close-button {
    position: absolute;
    top: 16px;
    right: 16px;
    background-color: #363636;
    color: #fff;
    padding: 8px;
    cursor: pointer;
    border: none;
    writing-mode: horizontal-tb;
  }
  
  button.toggle-button {
    position: absolute;
    top: 16px;
    left: 20px;
    background-color: #252525;
    color: #fff;
    padding: 8px;
    cursor: pointer;
    border: none;
    writing-mode: horizontal-tb;
    white-space: nowrap;
    z-index: 2;
  }
  
  .Title-text {
    font-family: 'Noto Sans JP', sans-serif;
    position: absolute;
    top: 33px;
    left: 60px;
    font-size: 24px;
    transform: translateY(-50%);
    margin-left: 8px;
    writing-mode: horizontal-tb;
    white-space: nowrap; /* テキストが横に改行されないようにする */
    z-index: 2;
  }

  .Title-Lesson-text {
    font-family: 'Noto Sans JP', sans-serif;
    position: absolute;
    top: 33px;
    left: 190px;
    font-size: 24px;
    transform: translateY(-50%);
    margin-left: 8px;
    writing-mode: horizontal-tb;
    white-space: nowrap; /* テキストが横に改行されないようにする */
    z-index: 2;
}

/* 半透明のボックス */
.overlay-box {
  position: absolute;
  top: 0px; /* タイトルの下に表示される位置を調整 */
  left: 0;
  width: 100%;
  height: 50px; /* 適宜調整 */
  background-color: rgba(255, 255, 255, 0.9); /* 半透明の白い色 */
  z-index: 1; /* タイトルより手前に表示する */
}

  /* サイドバー内のアイテムスタイル */
  ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
  }
  
  li {
    font-family: 'Noto Sans JP', sans-serif;
    padding: 8px;
    border-bottom: 1px solid #ccc;
    text-align: left; /* ボタンを左揃えにする */
  }
  
  li button {
    border: none;
    outline: 2px solid #00000000; /* アウトラインの色を指定 */
    outline-offset: 2px; /* アウトラインからボタンのコンテンツまでの距離 */
    text-align: left; /* ボタンを左揃えにする */
    transition: background-color 0.3s ease; /* 背景色変更のアニメーション効果を追加 */
  }
  
  /* マウスオーバー時のスタイル */
  li button:hover {
    outline-color: #8f8f8f; /* マウスオーバー時の背景色 */
  }
  
  /* クリック時のスタイル */
  li button:active {
    background-color: #8f8f8f00; /* クリック時の背景色 */
  }

  li:last-child {
    border-bottom: none;
  }
  